import React, { useState, useEffect } from "react";
import iframe from 'react-iframe';
import Button from "@material-ui/core/Button";
import PrintIcon from '@material-ui/icons/Print';

const styleIframe = {
    border: "0px",
    width: "100%",
    height: "100%",
    background: "white",
}

const styleModal = {
    width: "100%",
    height: "100%",
    background: "white",
};

const icon= {
    height: "50px",
    width: "50px",
};

const toggleButton= {
    border: 0,
    borderRadius: 0,
    backgroundColor: 'transparent',
    color: "#00152C",
    '&.Mui-selected': {
        backgroundColor: '#00152C',
        color: "white",
        '&:hover': {
            color: '#00152C',
            backgroundColor: '#A4BCCE',
        },
    },
    '&:hover': {
        color: '#00152C'
    },
};

const actions= {
    textAlign: "right",
    margin: "0px 6px",
    borderBottom: "1px solid black",
};

const HelpModal = () => {
    const [isLoading, setIsLoading] = useState(true);

    const handleMessage = (event) => {
        if (event.data.action === 'receipt-loaded') {
            setIsLoading(false);
        }
    };

    const printIframe = (id) => {
        const iframe = document.frames
            ? document.frames[id]
            : document.getElementById(id);
        const iframeWindow = iframe.contentWindow || iframe;

        iframe.focus();
        iframeWindow.print();

        return false;
    };

    useEffect(() => {
        window.addEventListener('message', handleMessage);

        return () => {
            window.removeEventListener('message', handleMessage);
        };
    }, []);

    return (
        <div style={styleModal}>
            <div style={ actions }>
                <Button value="Print" style={ toggleButton } onClick={() => printIframe('printHTML')}>
                    {isLoading ? <PrintIcon style={ icon }/> : 'Print Receipt'}
                </Button>
            </div>

            <iframe id="printHTML" src="./userGuide_IPC/en/Ineos_User_Guide.html" style={styleIframe} />
        </div>
    );
};

export default HelpModal;

