import React from 'react';
import Slider from '@material-ui/core/Slider';
import {
    withStyles, makeStyles, createTheme,
    ThemeProvider
} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        width: 300,
        // height: 73,
        border: 0,
        backgroundColor: '#00152C',
        borderRadius: 0,
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    slider: {
        marginLeft: "10px",
        width: "250px",
        direction: "rtl",
        color: 'white',
        height: 8,
        "& .MuiSlider-thumb": {
            height: 24,
            width: 24,
            backgroundColor: 'white',
            border: '2px solid currentColor',
            marginTop: -8,
            marginLeft: -12,
        },
        "& .MuiSlider-rail": {
            height: 8,
            borderRadius: 4,
        },
        "& .MuiSlider-track": {
            height: 8,
            borderRadius: 4,
        }
    },
}));


const theme = createTheme({
    direction: "rtl"
});

const ViewTransparency = (props) => {
    const classes = useStyles();

    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        try {
            if (window.pickedObject.handle) {
                setValue(-newValue);
                window.Cortona3DSolo.app.setObjectPropertyf(window.pickedObject.handle, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, true, newValue);
            }
        } catch (err) {
            setValue(0);
        }
    };

    return (
        <div className={classes.root}>
            <ThemeProvider theme={theme}>
                <Slider defaultValue={1}
                        valueLabelDisplay="auto"
                        step={1}
                        marks
                        min={1}
                        max={21}/>
            </ThemeProvider>
        </div>
    );
}

export default ViewTransparency;