import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import {createStyles, Theme, withStyles, WithStyles, makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import Cart from "./Cart";

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
    },
    root: {
        pointerEvents: "auto !important",
    },
    DialogAction: {
        justifyContent: "center"
    },
    DialogTitle: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        background: "#034E7A",
        color: "white"
    },
    DialogIcon: {
        width: "50px",
        height: "50px",
        margin: theme.spacing(2),
        fill: "black"
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dc:{
        width: "100%",
        height: "99%",
        // padding: 0
        // border: "2px solid black",
    }
}));


export default function ViewCart() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [srcDesc, setSrcDesc] = React.useState("");

    const resetPopup = () => {
        setSrcDesc("");
    }

    window.setCartPopup = function setCartPopup( imageDesc) {
        setOpen(true);
        setSrcDesc(imageDesc)
    }

    const handleClose = () => {
        resetPopup();
        setOpen(false);
    };

    return (
        <div id={"NoteDiv"} >
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" disableBackdropClick={true}
                    disableEscapeKeyDown={true} fullWidth={true} maxWidth={'xl'}>
                {/*<DialogTitle id="form-dialog-title" className={classes.DialogTitle}>*/}
                    <Typography variant="h5" >{srcDesc}</Typography>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                        <CloseIcon/>
                    </IconButton>
                {/*</DialogTitle>*/}
                <DialogContent className={classes.dc}>
                    <Cart />
                </DialogContent>
            </Dialog>
        </div>
    );
}
