import React, {useContext} from 'react';
import {PlusCircleIcon, MinusCircleIcon, TrashIcon} from '../Assets/icons'
import {CartContext} from "./CartContext"
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import RemoveIcon from '@material-ui/icons/Remove';
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";


const CartItem = ({product}) => {

    const {increase, decrease, removeProduct, setQuantity} = useContext(CartContext);

    const [check, setCheck] = React.useState(true);
    const [value, setValue] = React.useState(product.quantity);
    const handleChange = (event) => {
        setValue(event.target.value);

        if (event.target.value.length > 0 && event.target.value.length < 9) {
            setQuantity(product,event.target.value)
        }
        // if(value !== product.quantity){
        //     setValue(product.quantity)
        // }
    };

    return (

        <TableRow>
            <TableCell className="td-cart" align="center">{product.jobNumber}</TableCell>
            <TableCell className="td-cart" align="center">{product.part}</TableCell>
            <TableCell className="td-cart" align="center">{product.partDescription}</TableCell>
            <TableCell className="td-cart" align="center"><TextField id="quantityBox" size="small" onChange={handleChange} value={value}/></TableCell>
            <TableCell className="td-cart" align="center">                {
                product.quantity > 1 &&
                <button
                    onClick={() => removeProduct(product)}
                    style={{background: "white", borderStyle: "none"}}
                >
                    <TrashIcon width={"15px"}/>
                </button>
            }</TableCell>
            <TableCell className="td-cart" align="center">{(product.price)*(product.quantity)}</TableCell>
            <TableCell className="td-cart" align="center">{product.remarks}</TableCell>
        </TableRow>
    );
}
export default CartItem;

