import React from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Divider from '@material-ui/core/Divider';

export default function ContextMenuTable() {

    const [state, setState] = React.useState({
        mouseX: null,
        mouseY: null
    });
    const [sheet, setSheet] = React.useState([]);
    const [obj, setObj] = React.useState();
    const [Index, setIndex] = React.useState();

    window.setSheetMenuTable = function setSheetMenuTable(sheetName, sheetID) {
        console.log("name : "+ sheetName)
        setSheet(sheet.concat(<MenuItem value={sheetID} className={"ContextMenuItem"}
                                        onClick={() => handleSheetChange(sheetID)}>{sheetName}</MenuItem>));
    }
    window.setContextMenuTable = function setContextMenuTable(clientX, clientY, object, indexValue) {
        setState({
            mouseX: clientX - 2,
            mouseY: clientY - 4,
        });
        setObj(object);
        setIndex(indexValue);
        console.log(object);
    }

    const handleClose = () => {
        setState({
            mouseX: null,
            mouseY: null
        });
    };

    // Hide part
    const handleHidePart = () => {
        try {
            console.log("hide")
            console.log(Index)
            if (window.$('#chk' + (Index)).prop("checked") == true) {
                window.$('#chk' + (Index)).trigger('change').prop("checked", false);
            }
            handleClose();
        } catch (err) {
            handleClose();
        }
    }

    // Isolate part
    const handleIsolatePart = () => {
        try {
            //Reset
            window.Cortona3DSolo.app.ipc.resetCurrentSheet(false);
            window.Cortona3DSolo.app.restoreObjectProperty(null, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false);
            //Hide all
            // window.$("input[type=checkbox]").eq(0).prop("checked", false).change()
            // window.$("input[type=checkbox]").prop("checked", false);
            //Show only part
            // setTimeout(function () {
            //     window.$('#chk' + (Index)).trigger('change').prop("checked", true)
            // }, 100);
            //window.$('#chk' + (Index)).trigger('change').prop("checked", true);
            // }

            /* BI 4July22: Isolate */
            window.$('#dpl-holder > #dpl-table > tbody > tr').each(function () {
                if(window.$(this).attr('data-index')) {
                    let csnNo = window.$(this).find("td:nth-child(2) > pre").text();
                    let rowNo = window.Cortona3DSolo.app.ipc.interactivity.getIndexByItem(csnNo);

                    if(obj == csnNo){
                        // Show item
                        console.log('!isolate row ' +rowNo)
                    }
                    else {
                        // Hide item
                        window.Cortona3DSolo.app.ipc.toggleItemVisibility(rowNo)
                    }
                }
            })

            handleClose();
        } catch (err) {
            handleClose();
        }
    }

    // Add to basket
    // const handleAddToBasket = () => {
    //     try {
    //         window.$("img.add_basket[data-index=" + Index + "]").trigger('click')
    //         handleClose();
    //     } catch (err) {
    //         handleClose();
    //     }
    // }

    // Sheet Change
    const handleSheetChange = (sheetID) => {
        try {
            window.Cortona3DSolo.app.ipc.setCurrentSheet(sheetID, true);
            handleClose();
        } catch (err) {
            handleClose();
        }
    }

    // Reset View
    const handleResetView = () => {
        window.Cortona3DSolo.app.ipc.resetCurrentSheet(false);
        window.Cortona3DSolo.app.restoreObjectProperty(null, window.Cortona3DSolo.app.PROPERTY_TRANSPARENCY, false);
        window.$("input[type=checkbox]").prop('checked', "checked");
        handleClose();
    }

    // Copy part number
    let copyRow = '';
    const handleCopyPart = () => {
        window.$('#dpl-holder > #dpl-table > tbody > tr').each(function () {
            if(window.$(this).attr('data-index')) {
                let csnNo = window.$(this).find("td:nth-child(2) > pre").text();

                if(obj == csnNo){
                    copyRow = window.$(this).find("td:nth-child(3) > pre").text();

                    navigator.clipboard.writeText(copyRow).then(
                        function() {
                          window.alert('Copy! ' +copyRow) 
                        }, 
                        function() {
                          window.alert('Opps! Your browser does not support the Clipboard API')
                        }
                    )
                }
            }
        })

        
        handleClose();
    }

    return (
        <Menu
            keepMounted
            open={state.mouseY !== null}
            onClose={handleClose}
            anchorReference="anchorPosition"
            anchorPosition={
                state.mouseY !== null && state.mouseX !== null
                    ? {top: state.mouseY, left: state.mouseX}
                    : undefined
            }
        >
            {/*<MenuItem className={"ContextMenuItem"} onClick={handleAddToBasket}>Add To Basket</MenuItem>*/}
            {/*<Divider className={"ContextMenuDivider"}/>*/}
            <MenuItem key={1} className={"ContextMenuItem"} onClick={handleHidePart}>{window.contextHide}</MenuItem>
            <MenuItem key={2} className={"ContextMenuItem"} onClick={handleIsolatePart}>{window.contextIsolate}</MenuItem>
            <MenuItem key={3} className={"ContextMenuItem"} onClick={handleResetView}>{window.contextShowAll}</MenuItem>
            <MenuItem key={4} id="copyBtn" className={"ContextMenuItem"} onClick={handleCopyPart}>{window.contextCopy}</MenuItem>
            <Divider key={5} className={"ContextMenuDivider"}/>
            {sheet}
        </Menu>
        // </div>
    );
}