import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import {createStyles, Theme, withStyles, WithStyles, makeStyles} from '@material-ui/core/styles';
import DialogTitle from '@material-ui/core/DialogTitle';
import clsx from "clsx";
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) => ({
    margin: {
        margin: theme.spacing(1),
    },
    root: {
        pointerEvents: "auto !important",
    },
    DialogAction: {
        justifyContent: "center"
    },
    DialogTitle: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        background: "#034E7A",
        color: "white"
    },
    DialogIcon: {
        width: "50px",
        height: "50px",
        margin: theme.spacing(2),
        fill: "black"
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    img: {
        objectFit : "none",
        width: "100%",
        height: "-webkit-fill-available",
    },
    dc:{
        width: "99%",
        height: "99%",
        padding: 0,
        overflow: "hidden"
    }
}));


export default function View2DPopup() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [srcText, setSrcText] = React.useState("");
    const [srcDesc, setSrcDesc] = React.useState("");

    const resetPopup = () => {
        setSrcText("");
    }

    window.set2DPopup = function set2DPopup(imageSrc, imageDesc) {
        setOpen(true);
        setSrcText(imageSrc)
        setSrcDesc(imageDesc)
    }

    const handleClose = () => {
        resetPopup();
        setOpen(false);
    };

    return (
        <div id={"NoteDiv"}>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" disableBackdropClick={true}
                    disableEscapeKeyDown={true} className={classes.root}>
                <DialogTitle id="form-dialog-title" className={classes.DialogTitle}>
                    <Typography variant="h6">{srcDesc}</Typography>
                    <IconButton aria-label="close" className={classes.closeButton} onClick={handleClose}>
                        <CloseIcon/>
                    </IconButton>
                </DialogTitle>
                <DialogContent className={classes.dc}>
                    <img src={srcText} className={classes.img}/>
                </DialogContent>
            </Dialog>
        </div>
    );
}
