
const Storage = (cartItems) => {
    sessionStorage.setItem('RFQList', JSON.stringify(cartItems.length > 0 ? cartItems: []));
}


export const sumItems = cartItems => {
    Storage(cartItems);
    //let itemCount = cartItems.reduce((total, product) => total + product.quantity, 0);
    let itemCount = cartItems.length;
    // let itemCount = cartItems.reduce((a, b) =>{return a+b});
    let total = cartItems.reduce((total, product) => total + parseInt(product.quantity), 0);
    let totalPrice = cartItems.reduce((total, product) => total + (product.price * parseInt(product.quantity)), 0);
    return { itemCount, total,totalPrice }
}


export const CartReducer = (state, action) => {
    switch (action.type) {
        case "ADD_ITEM":
            if (!state.cartItems.find(item => item.part === action.payload.part)) {
                state.cartItems.push({
                    ...action.payload,
                    quantity: action.payload.quantity
                })
            }
            return {
                ...state,
                ...sumItems(state.cartItems),
                cartItems: [...state.cartItems]
            }
        case "REMOVE_ITEM":
            return {
                ...state,
                ...sumItems(state.cartItems.filter(item => item.part !== action.payload.part)),
                cartItems: [...state.cartItems.filter(item => item.part !== action.payload.part)]
            }
        case "INCREASE":
            state.cartItems[state.cartItems.findIndex(item => item.part === action.payload.part)].quantity++
            return {
                ...state,
                ...sumItems(state.cartItems),
                cartItems: [...state.cartItems]
            }
        case "DECREASE":
            state.cartItems[state.cartItems.findIndex(item => item.part === action.payload.part)].quantity--
            return {
                ...state,
                ...sumItems(state.cartItems),
                cartItems: [...state.cartItems]
            }
        case "CHECKOUT":
            return {
                cartItems: [],
                checkout: true,
                ...sumItems([]),
            }
        case "CLEAR":
                return {
                    cartItems: [],
                    ...sumItems([]),
                }
        case "setQuantity":
            state.cartItems[state.cartItems.findIndex(item => item.part === action.payload.part)].quantity = action.quantity
            return {
                ...state,
                ...sumItems(state.cartItems),
                cartItems: [...state.cartItems]
            }
        default:
            return state

    }
}