import React, {useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import MiniDrawer from "./MiniDrawer";
import Manual from "./Manual";
import {Box} from "@material-ui/core";
import Logo from "../Assets/INEOS-Automotive-Logo.svg";
import clsx from "clsx";
const useStyles = makeStyles(theme => ({
    root:{
        margin : 0,
        padding : 0,
        height: "100vh"
    },
    mainDiv:{
        height: "100vh",
        margin : 0,
        padding : 0,
        position: "relative",
    },
    manualDiv:{
        height: "100vh",
        margin : 0,
        padding : 0,
        position: "relative",
        display: 'flex',
        flexDirection: 'row',
    },
    Logo:{
        position: "absolute",
        zIndex: 100,
        right: 20,
        // top: 20,
        height: "auto",
        width: "auto",
        // margin: "10px",
        backgroundColor: "transparent"
    },
    displayComments:{
        position: "absolute",
        left: "10%",
        bottom: "3.5em",
        width: "80%",
        color: "black",
        textAlign: "center",
        zIndex: 100,
        '& p': {
            fontSize: "26px",
            fontWeight: "bold",
            textAlign: "center",
            margin: 0,
            padding: 0
        },
    },
    displayChapTitle:{
        position: "absolute",
        left: "10%",
        top: "1em",
        width: "80%",
        color: "black",
        textAlign: "center",
        zIndex: 100,
        '& p': {
            fontSize: "26px",
            fontWeight: "bold",
            textAlign: "center",
            margin: 0,
            padding: 0
        },
    },
    noShow:{
        display: "none"
    }
}));

export default function MainCanvas() {
    const classes = useStyles();

    const [manualWidth, setManualWidth] = useState(0.4);
    const [comments, setComments] = React.useState(false);
    const [chapTitle, setchapTitle] = React.useState(false);

    const logoPosition = (window.docLanguage === "ar-SA") ? "logo-left" : "logo-right";


    window.setManualDiv = function setManualDiv(valFlag){
        console.log("Manual Div State : " + valFlag)
        if(valFlag === true){
            setManualWidth(0.4);
            setComments(true);
            setchapTitle(true);
        }else{
            setManualWidth(0);
            setComments(false);
            setchapTitle(false);
        }
        setTimeout(function () {
            window.canvasResize();
        }, 10);
    }

    const handleClick = () => {
        // Handle click on the empty space of drawing
        if (!window.m_didSelectHotspotInvoked) {
            window.Cortona3DSolo.dispatch('app.drawing.didSelectHotspot');
        }
        window.m_didSelectHotspotInvoked = false;
    }


    return (
        <Box display="flex" p={1} bgcolor="background.paper" className={`AppChild ${classes.root}`} overflow="hidden">
            <Box p={1} flexGrow={1} bgcolor="white" className={classes.manualDiv} id={"MainDiv"} onClick={handleClick}>
            {/*<Box p={1} flexGrow={1} bgcolor="white" className={classes.manualDiv} id={"MainDiv"}>*/}
                <MiniDrawer/>
                <img src={Logo} className={`CompanyLogo ${classes.Logo} ${logoPosition}`}/>
                <div id="display-comments" className={clsx(classes.displayComments, {[classes.noShow]: comments})}></div>
                <div id="display-chapTitle" className={clsx(classes.displayChapTitle, {[classes.noShow]: comments})}></div>
                <div id="bottom-panel" className={clsx(classes.BottomPanel, {[classes.noShow]: comments})}></div>
            </Box>
            <Box id="TabDiv" p={1} width={manualWidth} bgcolor="#034E7A" whiteSpace="normal" className={`TabDiv ${classes.manualDiv}`}>
                <Manual/>
            </Box>
        </Box>
    );
}
