import React, {useContext} from 'react';
import CartProducts from "./CartProducts";
import {CartContext} from '../Components/CartContext';
import 'bootswatch/dist/lux/bootstrap.css';
// import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import {TrashIcon} from "../Assets/icons";

const useStyles = makeStyles({
    table: {
        minWidth: 650
    }
});

const Cart = () => {

    const {cartItems,itemCount, clearCart, checkout, handleCheckout, total, totalPrice} = useContext(CartContext);
    const classes = useStyles();

    return (
        <div>
                <TableContainer>
                    <Table className={classes.table} size="small" aria-label="simple table" style={{margin: "10px 0 0"}}>
                        <TableHead>
                            <TableRow style={{border: "5px solid #F0F5FA", background: "#00152C"}}>
                                <TableCell align="center" colSpan={7} style={{color: "white", fontWeight: "bold"}}>
                                    SHOPPING CART
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell className="td-cart-header" align="center">ITEM</TableCell>
                                <TableCell className="td-cart-header" align="center">PART NO.</TableCell>
                                <TableCell className="td-cart-header" align="center">DESCRIPTION</TableCell>
                                <TableCell className="td-cart-header" align="center" colSpan={2}>
                                    QTY
                                </TableCell>
                                <TableCell className="td-cart-header" align="center">PRICE</TableCell>
                                <TableCell className="td-cart-header" align="center">REMARKS</TableCell>
                            </TableRow>
                        </TableHead>

                            <CartProducts/>

                    </Table>
                </TableContainer>


            {/*<div className="col-sm-9 p-3">*/}
                {
                    cartItems.length < 1 &&
                        <div className="p-3 text-center text-muted">
                            Your cart is empty
                        </div>
                }

                {checkout &&
                <div className="p-3 text-center text-success">
                    <p>Checkout successfull</p>
                    {/*<Link to="/" className="btn btn-outline-success btn-sm">BUY MORE</Link>*/}
                </div>
                }


            <div className="card card-body">
                {/*<p className="mb-1">Total</p>*/}
                {/*<h4 className=" mb-3 txt-right">{itemCount}</h4>*/}
                {/*<hr className="my-4"/>*/}
                <div className="text-center">
                    <button type="button" className="btn btn-primary btn-sm btn-cart-upper"
                            onClick={handleCheckout}>CHECKOUT
                    </button>
                    <button type="button" className="btn btn-outlineprimary btn-sm btn-cart-upper"
                            onClick={clearCart}>CLEAR
                    </button>
                </div>
                <div className="text-center">
                    <button type="button" className="btn btn-primary btn-sm btn-cart-lower"
                            onClick={handleCheckout}>SAVE QUOTE
                    </button>
                    <button type="button" className="btn btn-outlineprimary btn-sm btn-cart-lower"
                            >PRINT
                    </button>
                </div>
            </div>
            <div className="card card-body-total">
                <p className="mb-1 text-cart">Total</p>
                <p className=" mb-3 txt-right text-cart">{total}</p>
                <p className="text-cart">{totalPrice}</p>
            </div>
        </div>
        // </div>


    );


}

export default Cart;