// import React from 'react';
import React, {useEffect} from 'react';
import './App.css';
import './printOutput.css';
import MainCanvas from "./Components/MainCanvas";
import CssBaseline from '@material-ui/core/CssBaseline';
import View2DPopup from "./Components/View2DPopup";
import ViewCart from "./Components/ViewCart";
import ContextMenuTable from "./Components/ContextMenuTable";
import Loading from "./Components/Loading";
import WebFont from "webfontloader";
import {ThemeProvider, createMuiTheme, createTheme} from '@material-ui/core/styles';
import PrintOutput from "./Components/PrintOutput";

const theme = createTheme({
    typography: {
        fontFamily: [
            'Open Sans',
            'Oswald',
        ].join(','),
    },
});

function App() {
    useEffect(() => {
        WebFont.load({
            google: {
                families: ["Open Sans", "Oswald"]
            }
        });
    }, []);
      return (
          <ThemeProvider theme={theme}>
            <div className="ScreenOnly">
                <div className="App" id="App">
                    <CssBaseline />
                    <MainCanvas />
                    <View2DPopup />
                    <ViewCart />
                    <ContextMenuTable />
                    <Loading/>
                </div>
            </div>
            <div className="PrintOnly"><PrintOutput /></div>
          </ThemeProvider>
      );
}

export default App;
