import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Box} from "@material-ui/core";
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';

const useStyles = makeStyles((theme) => ({
    content:{
      position: "relative"
    },
    root: {
        display: 'flex',
        alignItems: 'center',
        backgroundColor: 'white',
        justifyContent: 'center',
        flexDirection: 'column',
        alignContent: "center",
        position: "absolute",
        top : "50%",
        left : "50%",
        transform: "translate(-50%, -50%)"
    },
    buttonProgress: {
        color: '#034D79'
    },
}));

export default function CircularIndeterminate() {
    const classes = useStyles();
    const [load, setLoading] = React.useState(true);

    window.hideLoading = function hideLoading(){
        setLoading(false);
    }

    window.showLoading = function showLoading(){
        setLoading(true);
    }

    return (
            <Dialog fullScreen open={load} aria-labelledby="form-dialog-title" disableBackdropClick={true}
                    disableEscapeKeyDown={true} maxWidth="md">

                <DialogContent>
                    <div className={classes.root} >
                        <div className={"loadingtitle"}>{window.contextLoading}</div>
                        <CircularProgress size={100} class="loading_progress"/>
                    </div>
                </DialogContent>
            </Dialog>

    );
}