import React from "react";
import {makeStyles} from "@material-ui/core/styles";

import Logo from "../Assets/Ineos-Automotive-logo.png";

const useStyles = makeStyles((theme) => ({
    [`@media screen`]: { },
    [`@media print`]: {
        HeaderLogo: {
            margin: '4mm',
            height: '24mm',
        },
    },
}));

const PrintOutput = () => {
    const classes = useStyles();

    const ConvertToArabicNumbers = (num) => {
        const arabicNumbers = '\u0660\u0661\u0662\u0663\u0664\u0665\u0666\u0667\u0668\u0669';
        return new String(num).replace(/[0123456789]/g, (d)=>{return arabicNumbers[d]});
    }

    let nowDate;
    let get_Date = new Date();
    let get_Month = new Date();
    get_Month = get_Month.getMonth() +1
    if(window.docLanguage === "ar-SA"){
        let year = ConvertToArabicNumbers(get_Date.getFullYear())
        nowDate = `${get_Date.getDate().toLocaleString("ar-SA")} / ${get_Month.toLocaleString("ar-SA")} / ${year}`
    } else if(window.docLanguage === 'de-DE') {
        nowDate = `${get_Date.getDate().toLocaleString("de-DE")} / ${get_Month.toLocaleString("de-DE")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'it-IT') {
        nowDate = `${get_Date.getDate().toLocaleString("it-IT")} / ${get_Month.toLocaleString("it-IT")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'fr-FR') {
        nowDate = `${get_Date.getDate().toLocaleString("fr-FR")} / ${get_Month.toLocaleString("fr-FR")} / ${get_Date.getFullYear()}`
    } else if(window.docLanguage === 'es-ES') {
        nowDate = `${get_Date.getDate().toLocaleString("es-ES")} / ${get_Month.toLocaleString("es-ES")} / ${get_Date.getFullYear()}`
    } else {
        nowDate = `${get_Date.getDate().toLocaleString("en-US")} / ${get_Month.toLocaleString("en-US")} / ${get_Date.getFullYear()}`
    }

    const pageDynamic = () => {
        return (
            <table className="pageDynamic">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className={classes.HeaderLogo} /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div className="printContent"></div>
                </tbody>
                <tfoot>
                    <div className="footerTag">
                        <div className="Date">{nowDate}</div>
                        {pageNum()}
                    </div>
                </tfoot>
            </table>
        )
    }

    let pageNumber = 0;
    const pageNum = () => {
        let number = 0;
        pageNumber = pageNumber +1;
        if(window.docLanguage === "ar-SA") {
            return (
                <div className={"page"}>
                     {ConvertToArabicNumbers(pageNumber)}
                </div>
            );
        } else if(window.docLanguage === 'de') {
            number = 'Seite ' +pageNumber;
        } else if(window.docLanguage === 'it') {
            number = 'Pagina ' +pageNumber;
        } else if(window.docLanguage === 'fr') {
            number = 'Page ' +pageNumber;
        } else if(window.docLanguage === 'es') {
            number = 'Pao ' +pageNumber;
        } else {
            number = 'Page ' +pageNumber;
        }
        return (
            <div className={"page"}>
                {number}
            </div>
        );
    }

    const headerDetail = () => {
        if(window.docLanguage === "ar-SA") {
            return (
                <div className="Page-Detail" style={{ borderRight: '1px solid black'}}>
                    <div className="Header-Row1" />
                    <div className="Header-Row2" />
                    <div className="Header-Row3" />
                </div>
            );
        } else {
            return (
                <div className="Page-Detail" style={{ borderLeft: '1px solid black'}}>
                    <div className="Header-Row1" />
                    <div className="Header-Row2" />
                    <div className="Header-Row3" />
                </div>
            );
        }
    }

    const partTitle = () => {
        if(window.docLanguage === "ar-SA") {
            return (
                <h5 className="Page-Heading1" style={{ fontFamily: 'Arial', textAlign: 'right' }}>{window.partsTab}</h5>
            );
        } else {
            return (
                <h5 className="Page-Heading1" style={{ fontFamily: 'Arial', textAlign: 'left' }}>{window.partsTab}</h5>
            );
        }
    }

    return (
        <div className="PrintOutput">
            <table className="tablePage image2D">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className={classes.HeaderLogo} /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div className="printContent">
                        <div id="CurrentView"/>
                    </div>
                </tbody>
                <tfoot>
                    <div className="Page-Footer">
                        <div className="Date">{nowDate}</div>
                        {pageNum()}
                    </div>
                </tfoot>
            </table>
            <table className="tablePage">
                <thead>
                    <div className="Page-Header">
                        <div className="Page-Logo"><img src={Logo} className={classes.HeaderLogo} /></div>
                        {headerDetail()}
                    </div>
                </thead>
                <tbody>
                    <div className="printContent">
                        {partTitle()}
                        <div id="partsTable" />
                    </div>
                </tbody>
                <tfoot>
                    <div className="Page-Footer">
                        <div className="Date">{nowDate}</div>
                        {pageNum()}
                    </div>
                </tfoot>
            </table>

            {/* dynamic page template */}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}
            {pageDynamic()}

        </div>
    );
};

export default PrintOutput;

