import React from 'react';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import IconButton from '@material-ui/core/IconButton';
import {makeStyles} from "@material-ui/core/styles";
import Aim from "../Assets/aim.svg";
import Cartesian from "../Assets/cartesian.svg";
import FullScreen from "../Assets/fullscreen.svg";
import ObjectAlignment from "../Assets/objectalignment.svg";

const useStyles = makeStyles((theme) => ({
    icon:{
        height: "49px",
        width: "50px",
    },
    groupButton:{
        border: 0,
        backgroundColor: '#00152C',
        borderRadius: 0,
        '& img':{
            filter: "brightness(0) invert(1)",
            transform: "scale(0.8)"
        },
    },
    iconButton:{
        borderRadius: 0,
        zIndex:1000,
        '&:hover': {
            backgroundColor: '#A4BCCE'
        },
    },
    activeBtn: {
        borderRadius: 0,
        zIndex:1000,
        backgroundColor: '#A4BCCE',
    },
}));

const View3D = (props) => {
    const classes = useStyles();

    const handleFullScreen = () =>{
        if( window.innerHeight == window.screen.height) {
            // browser is fullscreen
            window.closeFullscreen();
        }
        else{
            window.openFullscreen();
        }
    }

    const handleSetCenter = () =>{
        // Click on 3D object
        if(window.$("#Cartesian").hasClass(classes.activeBtn)){
            window.Cortona3DSolo.app.setRotationCenterVisibility(true);
            window.Cortona3DSolo.app.setRotationCenterToSelectedObjects(true);
        }
    }
    const handleAlignHorizon = () =>{
        window.Cortona3DSolo.app.alignHorizon(true);
    }
    const handleCartesian = () =>{
        if(window.Cortona3DSolo.app.isRotationCenterVisible() === true){
            window.Cortona3DSolo.app.setRotationCenterVisibility(false);
            window.Cortona3DSolo.app.setRotationCenterToSelectedObjects(false);
        }else{
            window.Cortona3DSolo.app.setRotationCenterVisibility(true);
            window.Cortona3DSolo.app.setRotationCenterToSelectedObjects(true);
        }
    }
    function view3D (btnID) {
        console.log("btnTag " + btnID);

        if (btnID === "SetCenter"){
            handleSetCenter();
        } else if (btnID === "Cartesian"){
            if(window.$("#" + btnID).hasClass(classes.activeBtn)){
                window.$("#" + btnID).removeClass(classes.activeBtn);
            } else {
                // window.$("#btn3DView > button").removeClass(classes.activeBtn);
                window.$("#" + btnID).addClass(classes.activeBtn);
            }
            handleCartesian();
        } else if (btnID === "FullScreen"){
            if(window.$("#" + btnID).hasClass(classes.activeBtn)){
                window.$("#" + btnID).removeClass(classes.activeBtn);
            } else {
                // window.$("#btn3DView > button").removeClass(classes.activeBtn);
                window.$("#" + btnID).addClass(classes.activeBtn);
            }
            handleFullScreen();
        } else if (btnID === "AlignHorizon"){
            if(window.$("#" + btnID).hasClass(classes.activeBtn)){
                window.$("#" + btnID).removeClass(classes.activeBtn);
            } else {
                // window.$("#btn3DView > button").removeClass(classes.activeBtn);
                window.$("#" + btnID).addClass(classes.activeBtn);
            }
            handleAlignHorizon();
        }
    }
    return (
        <ButtonGroup id="btn3DView" className={classes.groupButton}>
            <IconButton className={`_3dViewBtn ${classes.iconButton}`} id="SetCenter" title={"Set Center"} onClick={(btnID) => view3D("SetCenter") }>
                <img src={Aim} className={`_3dViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton className={`_3dViewBtn ${classes.iconButton}`} id={"Cartesian"} title={"Show/Hide Horizon Center"} onClick={(btnID) => view3D("Cartesian") }>
                <img src={Cartesian} className={`_3dViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton className={`_3dViewBtn ${classes.iconButton}`} id="FullScreen" title={"Full Screen"} onClick={(btnID) => view3D("FullScreen") }>
                <img src={FullScreen} className={`_3dViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton className={`_3dViewBtn ${classes.iconButton}`} id="AlignHorizon" title={"Align Horizon"} onClick={(btnID) => view3D("AlignHorizon") }>
                <img src={ObjectAlignment} className={`_3dViewSvg ${classes.icon}`}/>
            </IconButton>
        </ButtonGroup>
    );
}

export default View3D;