import React from 'react';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import IconButton from '@material-ui/core/IconButton';
import {makeStyles} from "@material-ui/core/styles";
import Front_View from "../Assets/Front_View.svg";
import Back_View from "../Assets/Back_View.svg";
import Left_Side_View from "../Assets/Left_Side_View.svg";
import Right_Side_View from "../Assets/Right_Side_View.svg";
import Top_View from "../Assets/Top_View.svg";
import Bottom_View from "../Assets/Bottom_View.svg";
import View_Multi from "../Assets/cube.svg";

const useStyles = makeStyles((theme) => ({
    icon: {
        height: "49px",
        width: "50px",
    },
    groupButton: {
        border: 0,
        backgroundColor: '#00152C',
        borderRadius: 0,
        '& img': {
            filter: "brightness(0) invert(1)",
            transform: "scale(0.8)"
        },
    },
    iconButton: {
        borderRadius: 0,
        zIndex:1000,
        '&:hover': {
            backgroundColor: '#A4BCCE'
        },
    },
}));

function viewPlan (side, btnID) {
    window.Cortona3DSolo.app.jumpToStandardView(side, true)
    console.log("btnTag " + btnID);

    window.$("#btnViewMulti").children().removeClass("bgSelect");
    window.$("#" + btnID).addClass("bgSelect");
}

const ViewMulti = (props) => {
    const classes = useStyles();
    return (
        <ButtonGroup id="btnViewMulti" className={classes.groupButton}>
            <IconButton id="Front" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("front", "Front") } title={"Front View"}>
                <img src={Front_View} alt={Front_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Back" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("back", "Back") } title={"Back View"}>
                <img src={Back_View} alt={Back_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Left" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("left", "Left") } title={"Left View"}>
                <img src={Left_Side_View} alt={Left_Side_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Right" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("right", "Right") } title={"Right View"}>
                <img src={Right_Side_View} alt={Right_Side_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Top" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("top", "Top") } title={"Top View"}>
                <img src={Top_View} alt={Top_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Bottom" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("bottom", "Bottom") } title={"Bottom View"}>
                <img src={Bottom_View} alt={Bottom_View} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
            <IconButton id="Isometric" className={`MultiViewBtn ${classes.iconButton}`} onClick={(side, btnID) => viewPlan("isometric", "Isometric") } title={"Isometric"}>
                <img src={View_Multi} alt={View_Multi} className={`MultiViewSvg ${classes.icon}`}/>
            </IconButton>
        </ButtonGroup>
    );
}

export default ViewMulti;