import React from 'react';
import PropTypes from 'prop-types';
import {makeStyles, useTheme} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

import {withStyles} from "@material-ui/core";


const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: "#BAD0DB",
        // height: "100%",
        width: "100%"
    }, tab: {
        backgroundColor: "#BAD0DB",
        height: "100%",
        width: "100%"

    },
    Title: {
        color: 'white',
        fontWeight: "bold"
    },
    indtab: {
        backgroundColor: "#BAD0DB",
        borderTop: "none",
        borderBottom: "solid 5px white",
        color: "black",
        fontSize: "14px",
        textTransform: "capitalize"
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
}));

const GlobalCss = withStyles({
    '@global': {
        '.Mui-selected': {
            backgroundColor: "#00152C",
            color: "#FFFFFF",
            fontWeight: "bold",
        },
    },
})(() => null);

function TabPanel(props) {
    const {children, value, index, ...other} = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Container>
                    <Box>
                        {children}
                    </Box>
                </Container>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};


function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

export default function Manual() {
    const [value, setValue] = React.useState(0);
    const classes = useStyles();

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };


    var textDirection = (window.docLanguage === "ar-SA") ? 'rtl' : 'ltr';
    var textDirectionCenter = (window.docLanguage === "ar-SA") ? 'rtl-center' : 'ltr-center';

    return (
        <div className={classes.tab}>
            <AppBar position="static" color="default" style={{boxShadow: 'none'}}>
                <GlobalCss/>
                <Tabs value={value} onChange={handleChange} indicatorColor="white" textColor="white" variant="fullWidth"
                      aria-label="full width tabs example">
                    <Tab className={`tab-title ${classes.indtab} ${textDirectionCenter}`} style={window.docLanguage === "ar-SA" ? {borderLeft: 'solid 5px white'} : {borderRight: 'solid 5px white'}}
                         label={window.partsTab} {...a11yProps(0)} />
                    <Tab className={`tab-title ${classes.indtab}`}
                         label={window.detailsTab} {...a11yProps(1)} />
                </Tabs>
            </AppBar>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 0}
                id="tab-content-0"
                className={"tab-content"}
            >
                <div id="dpl" className={classes.root}>
                <div id="dpl-holder" className="container"></div>
                </div>
            </Typography>

            <Typography
                component="div"
                role="tabpanel"
                hidden={value !== 1}
                id="tab-content-1"
                className={"tab-content"}
                // onClick={handleTabContentClick}
            >
                <TableContainer style={{backgroundColor: 'inherit', boxShadow: 'none', marginTop: '2%'}}
                                component={Paper} id="TabDetails">
                    <Table style={{width: '96%', marginLeft: '2%'}} className={classes.table} size="small"
                           aria-label="simple table">
                        <TableBody>
                            { window.titleData && <TableRow className="description-row">
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.titleHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`} scope="row"
                                           align="center"> {window.titleData}
                                </TableCell>
                            </TableRow> }
                            { window.sectionData && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.sectionHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`} scope="row"
                                           align="center">{window.sectionData}
                                </TableCell>
                            </TableRow> }
                            { window.docNoData && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.docNoHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.docNoData}</TableCell>
                            </TableRow> }
                            { window.modelData && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.modelHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.modelData}</TableCell>
                            </TableRow> }
                            { window.modelYearData && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.modelYearHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.modelYearData}</TableCell>
                            </TableRow> }
                            { (window.engineData && window.engineData != 'Common') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.engineHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.engineData}</TableCell>
                            </TableRow> }
                            { (window.transmissionData && window.transmissionData != 'Common') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.transmissionHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.transmissionData}</TableCell>
                            </TableRow> }
                            { (window.lhdRhdData && window.lhdRhdData != 'Common') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th" align="center">{window.lhdHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.lhdRhdData}</TableCell>
                            </TableRow> }
                            {(window.bodyData && window.bodyData != 'Common') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.bodyHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.bodyData}</TableCell>
                            </TableRow>}
                            {(window.regionData && window.regionData != 'Common') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.regionHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical ${textDirection}`}
                                           align="center">{window.regionData}</TableCell>
                            </TableRow>}
                            {(window.vinFromData && window.vinFromData != 'XXXXXXXXXXXXXXXXX') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.vinFromHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical td-data ${textDirection}`}
                                           align="center">{window.vinFromData}</TableCell>
                            </TableRow>}
                            {(window.vinToData && window.vinToData != 'XXXXXXXXXXXXXXXXX') && <TableRow>
                                <TableCell className={`dpl-th-vertical ${textDirection}`} component="th"
                                           align="center">{window.vinToHeader}</TableCell>
                                <TableCell colSpan={2} className={`dpl-td-vertical td-data ${textDirection}`}
                                           align="center">{window.vinToData}</TableCell>
                            </TableRow>}
                            { window.createdDateData && <TableRow>
                                <TableCell className={`dpl-th-vertical revision-header ${textDirection}`} component="th"
                                           align="center">{window.revisionsHeader}</TableCell>
                                <TableCell className={`dpl-td-vertical revision-row-title ${textDirection}`}
                                           align="center">{window.createdHeader}</TableCell>
                                <TableCell className={`dpl-td-vertical revision-data td-data ${textDirection}`}
                                           align="center">{window.createdDateData}</TableCell>
                            </TableRow>}

                            { window.revisionDateData && <TableRow>
                                <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                                <TableCell className={`dpl-td-vertical revision-row-title ${textDirection}`}
                                           align="center">{window.revisedHeader}</TableCell>
                                <TableCell className={`dpl-td-vertical revision-data td-data ${textDirection}`}
                                           align="center">{window.revisionDateData}</TableCell>
                            </TableRow> }

                            { window.revisionData && <TableRow>
                                <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                                <TableCell className={`dpl-td-vertical revision-row-title ${textDirection}`}
                                           align="center">{window.commentsHeader}</TableCell>
                                <TableCell className={`dpl-td-vertical revision-data td-data ${textDirection}`}
                                           align="center">{window.revisionData}</TableCell>
                            </TableRow> }
                        </TableBody>
                    </Table>
                {/*    <Table style={{width: '96%', marginLeft: '2%'}} className={classes.table} size="small" aria-label="simple table" id="revision-table-row">
                        <TableBody>
                            <TableRow>
                                <TableCell className="dpl-th-vertical revision-header" component="th"
                                           align="center">Revisions</TableCell>
                                <TableCell className="dpl-td-vertical revision-row-title"
                                           align="center">Created</TableCell>
                                <TableCell className="dpl-td-vertical revision-data"
                                           align="center">{window.createdDateData}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                                <TableCell className="dpl-td-vertical revision-row-title"
                                           align="center">Revised</TableCell>
                                <TableCell className="dpl-td-vertical revision-data"
                                           align="center">{window.revisionDateData}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell className="hide-header" style={{border: 'none'}} align="center"></TableCell>
                                <TableCell className="dpl-td-vertical revision-row-title"
                                           align="center">Comments</TableCell>
                                <TableCell className="dpl-td-vertical revision-data"
                                           align="center">{window.revisionData}</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>    */}
                </TableContainer>
            </Typography>

        </div>
    );
}
